.container {
  max-width: 1280px;
  margin-inline: auto;
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 39px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    margin-inline: auto;
    padding-inline: 55px;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
    transform: translateX(0)translateY(-50%);
  }

  50% {
    opacity: .5;
    transform: translateX(0)translateY(-80%);
  }

  100% {
    opacity: 1;
    transform: translateX(0)translateY(0);
  }
}

@keyframes move {
  0% {
    transform: translateX(0);
  }

  50% {
    transform-origin: 0;
    transform: translateX(0)scale(1.5);
  }

  100% {
    transform-origin: 100%;
    transform: translateX(0)scale(1.5);
  }
}

@keyframes moveBtn {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100%);
  }
}

.info--footer, .footer__bottom, .signing__text, .signing__container, .events__content, .main__news, .page__menu {
  flex-direction: column;
  display: flex;
}

.footer__bottom__text, .footer__socials, .footer__top, .signing__input, .events__date, .menu__top, .tickets {
  flex-direction: row;
  display: flex;
}

@font-face {
  font-family: Montserrat;
  src: url("Montserrat-VariableFont_wght.c16ad322.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
