.page {
  scroll-behavior: smooth;
}

.page__darken {
  z-index: 200;
  opacity: 0;
  pointer-events: none;
  background-color: #000;
  width: 100%;
  height: 100%;
  transition: opacity .5s cubic-bezier(.39, .57, .56, 1);
  position: absolute;
  top: 0;
  bottom: 0;
}

.page__body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  position: relative;
}

.page__body:has(.page__menu:target) {
  overflow: hidden;
}

.page__body:has(.page__menu:target) .page__darken {
  pointer-events: all;
  opacity: .7;
}

.page__menu {
  z-index: 1000;
  pointer-events: none;
  background-color: #1a5a4c;
  gap: 24px;
  padding-block: 27px;
  padding-inline: 20px;
  transition: all .5s cubic-bezier(.39, .57, .56, 1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-101%);
}

@media (width >= 640px) {
  .page__menu {
    padding-inline: 39px;
  }
}

@media (width >= 1280px) {
  .page__menu {
    padding-inline: 55px;
  }
}

.page__menu:target {
  pointer-events: all;
  transform: translateY(0);
}

@media (width >= 640px) {
  .page__menu {
    flex-direction: row;
    gap: 38px;
    padding-block: 39px;
  }
}

@media (width >= 1280px) {
