}

.icon--header:after {
  content: "";
  opacity: 0;
  pointer-events: none;
  background-color: #0f0e08;
  background-clip: content-box;
  border: 1px solid #0f0e08;
  border-radius: 50%;
  width: 50%;
  height: 50%;
  padding: 5px;
  transition: all .5s cubic-bezier(.39, .57, .56, 1);
  position: absolute;
  bottom: 0;
  transform: scale(.5);
}

.icon--header:hover:after {
  opacity: 1;
  pointer-events: none;
  transform: scale(.9)rotate(360deg);
}

.icon--header:hover .icon--burger-menu {
  opacity: 0;
  transform: rotate(360deg)scale(0);
}

.icon--burger-menu {
  background-image: url("icon-burger-menu.7e2755ef.svg");
  transition: all .5s cubic-bezier(.39, .57, .56, 1);
}

.icon--close {
